<script>

import {mapState,mapMutations, mapActions} from 'vuex';
export default {
    name:'Home',
    data(){
        return{
          categorias:[],
          cantidad:[],
          productos:[],
          prodAdd:{},
          mensaje: {color: '', texto: ''},
          dismissSecs: 2,
          dismissCountDown: 0 
        }
    },
    computed:{
      ...mapState(['estaCompra','total','Usuario'])
    },
    created(){
        this.ObtenerProductos();
    },
    methods:{
      ...mapMutations(['AgregarAlCarrito','AgregarOrden','AggCompra']),
        ObtenerProductos(){
           this.axios.get('/productos') 
             .then(res=>{
                this.productos=res.data;
                for(var item in this.productos){
                  this.cantidad.push(1)
                }
                
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        compra(id,indice){
          var cate=false;
          this.axios.get(`/producto/${id}`)
            .then(res=>{
              this.prodAdd=res.data;
              this.$store.commit('AgregarAlCarrito',[this.prodAdd,this.cantidad[indice]]);
              this.$store.commit('AgregarOrden',[this.prodAdd,this.cantidad[indice]]);
              this.cantidad[indice]=1;
              for(var item in this.categorias){
                
                if(this.categorias[item].ordenenPrevia==this.prodAdd.categoryID){
                   cate=true;
                }
              }
              if(!cate){
                var categoriavisitada=this.prodAdd.categoryID;
                this.categorias.push({categoriavisitada});
                this.$store.commit('AggCompra',this.categorias);
              
              }
              
              this.mensaje.color='success';
              this.mensaje.texto=this.prodAdd.name + ' Agregado Al Carrito';
              this.showAlert();
              alert(this.prodAdd.name +' Agregado al carrito');
            })
            .catch(e=>{
              console.log(e.response,"error")
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
    }

}
</script>

